/**
 * ã‚·ãƒ¼ãƒ‰å€¤ã‚’ä½¿ç”¨ã—ã¦é…åˆ—ã‚’ã‚·ãƒ£ãƒƒãƒ•ãƒ«ã™ã‚‹ï¼ˆæ±ºå®šè«–çš„ï¼‰
 * åŒã˜ã‚·ãƒ¼ãƒ‰å€¤ã§ã‚ã‚Œã°å¸¸ã«åŒã˜é †åºã«ãªã‚‹
 */
export function shuffleArrayWithSeed<T>(array: T[], seed: number): T[] {
  const shuffled = [...array];
  let currentIndex = shuffled.length;

  // ã‚·ãƒ¼ãƒ‰ã‚’ä½¿ç”¨ã—ã¦ç–‘ä¼¼ãƒ©ãƒ³ãƒ€ãƒ æ•°ã‚’ç”Ÿæˆ
  const random = (index: number): number => {
    const x = Math.sin(seed + index) * 10000;
    return x - Math.floor(x);
  };

  while (currentIndex > 0) {
    const randomIndex = Math.floor(random(currentIndex) * currentIndex);
    currentIndex--;
    [shuffled[currentIndex], shuffled[randomIndex]] = [
      shuffled[randomIndex],
      shuffled[currentIndex],
    ];
  }

  return shuffled;
}

/**
 * ç¾åœ¨ã®æ™‚é–“ã«åŸºã¥ã„ã¦ã‚·ãƒ£ãƒƒãƒ•ãƒ«ç”¨ã®ã‚·ãƒ¼ãƒ‰å€¤ã‚’ç”Ÿæˆ
 * 1æ™‚é–“ã”ã¨ã«ç•°ãªã‚‹ã‚·ãƒ¼ãƒ‰å€¤ã«ãªã‚‹
 */
export function getHourlyShuffleSeed(): number {
  return Math.floor(Date.now() / (1000 * 60 * 60));
}

/**
 * é…åˆ—ã‚’æŒ‡å®šã•ã‚ŒãŸãƒšãƒ¼ã‚¸ã‚µã‚¤ã‚ºã§ãƒšãƒ¼ã‚¸åˆ†å‰²ã™ã‚‹
 */
export function paginateArray<T>(array: T[], pageSize: number, currentPage: number): T[] {
  const startIndex = currentPage * pageSize;
  const endIndex = startIndex + pageSize;
  return array.slice(startIndex, endIndex);
}

/**
 * ã‚«ãƒ†ã‚´ãƒªãƒ¼ã§ã‚¢ã‚¤ãƒ†ãƒ ã‚’ãƒ•ã‚£ãƒ«ã‚¿ãƒªãƒ³ã‚°ã™ã‚‹
 */
export function filterByCategory<T extends { category: string }>(
  items: T[],
  category: string,
): T[] {
  if (category === 'all') {
    return items;
  }
  return items.filter((item) => item.category === category);
}
