import Decimal from 'decimal.js-light';
import { curry } from './utils';
/**
 * è·å–æ•°å€¼çš„ä½æ•°
 * å…¶ä¸­ç»å¯¹å€¼å±äºåŒºé—´[0.1, 1)ï¼Œ å¾—åˆ°çš„å€¼ä¸º0
 * ç»å¯¹å€¼å±äºåŒºé—´[0.01, 0.1)ï¼Œå¾—åˆ°çš„ä½æ•°ä¸º -1
 * ç»å¯¹å€¼å±äºåŒºé—´[0.001, 0.01)ï¼Œå¾—åˆ°çš„ä½æ•°ä¸º -2
 *
 * @param  {Number} value æ•°å€¼
 * @return {Integer} ä½æ•°
 */

function getDigitCount(value) {
  var result;

  if (value === 0) {
    result = 1;
  } else {
    result = Math.floor(new Decimal(value).abs().log(10).toNumber()) + 1;
  }

  return result;
}
/**
 * æŒ‰ç…§å›ºå®šçš„æ­¥é•¿è·å–[start, end)è¿™ä¸ªåŒºé—´çš„æ•°æ®
 * å¹¶ä¸”éœ€è¦å¤„ç†jsè®¡ç®—ç²¾åº¦çš„é—®é¢˜
 *
 * @param  {Decimal} start èµ·ç‚¹
 * @param  {Decimal} end   ç»ˆç‚¹ï¼Œä¸åŒ…å«è¯¥å€¼
 * @param  {Decimal} step  æ­¥é•¿
 * @return {Array}         è‹¥å¹²æ•°å€¼
 */


function rangeStep(start, end, step) {
  var num = new Decimal(start);
  var i = 0;
  var result = []; // magic number to prevent infinite loop

  while (num.lt(end) && i < 100000) {
    result.push(num.toNumber());
    num = num.add(step);
    i++;
  }

  return result;
}
/**
 * å¯¹æ•°å€¼è¿›è¡Œçº¿æ€§æ’å€¼
 *
 * @param  {Number} a  å®šä¹‰åŸŸçš„æç‚¹
 * @param  {Number} b  å®šä¹‰åŸŸçš„æç‚¹
 * @param  {Number} t  [0, 1]å†…çš„æŸä¸ªå€¼
 * @return {Number}    å®šä¹‰åŸŸå†…çš„æŸä¸ªå€¼
 */


var interpolateNumber = curry(function (a, b, t) {
  var newA = +a;
  var newB = +b;
  return newA + t * (newB - newA);
});
/**
 * çº¿æ€§æ’å€¼çš„é€†è¿ç®—
 *
 * @param  {Number} a å®šä¹‰åŸŸçš„æç‚¹
 * @param  {Number} b å®šä¹‰åŸŸçš„æç‚¹
 * @param  {Number} x å¯ä»¥è®¤ä¸ºæ˜¯æ’å€¼åçš„ä¸€ä¸ªè¾“å‡ºå€¼
 * @return {Number}   å½“xåœ¨ a ~ bè¿™ä¸ªèŒƒå›´å†…æ—¶ï¼Œè¿”å›å€¼å±äº[0, 1]
 */

var uninterpolateNumber = curry(function (a, b, x) {
  var diff = b - +a;
  diff = diff || Infinity;
  return (x - a) / diff;
});
/**
 * çº¿æ€§æ’å€¼çš„é€†è¿ç®—ï¼Œå¹¶ä¸”æœ‰æˆªæ–­çš„æ“ä½œ
 *
 * @param  {Number} a å®šä¹‰åŸŸçš„æç‚¹
 * @param  {Number} b å®šä¹‰åŸŸçš„æç‚¹
 * @param  {Number} x å¯ä»¥è®¤ä¸ºæ˜¯æ’å€¼åçš„ä¸€ä¸ªè¾“å‡ºå€¼
 * @return {Number}   å½“xåœ¨ a ~ bè¿™ä¸ªåŒºé—´å†…æ—¶ï¼Œè¿”å›å€¼å±äº[0, 1]ï¼Œ
 * å½“xä¸åœ¨ a ~ bè¿™ä¸ªåŒºé—´æ—¶ï¼Œä¼šæˆªæ–­åˆ° a ~ b è¿™ä¸ªåŒºé—´
 */

var uninterpolateTruncation = curry(function (a, b, x) {
  var diff = b - +a;
  diff = diff || Infinity;
  return Math.max(0, Math.min(1, (x - a) / diff));
});
export default {
  rangeStep: rangeStep,
  getDigitCount: getDigitCount,
  interpolateNumber: interpolateNumber,
  uninterpolateNumber: uninterpolateNumber,
  uninterpolateTruncation: uninterpolateTruncation
};