/**
 * ãƒ†ã‚­ã‚¹ãƒˆã‹ã‚‰è¨€èªã‚’è‡ªå‹•æ¤œå‡ºã™ã‚‹
 * @param text æ¤œå‡ºå¯¾è±¡ã®ãƒ†ã‚­ã‚¹ãƒˆ
 * @returns 'ja' | 'en'
 */
export function detectLanguage(text: string): 'ja' | 'en' {
  // ç©ºæ–‡å­—ã‚„ã‚¹ãƒšãƒ¼ã‚¹ã®ã¿ã®å ´åˆã¯è‹±èªã‚’ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆã¨ã™ã‚‹
  const trimmedText = text.trim();
  if (!trimmedText) {
    return 'en';
  }

  // ã²ã‚‰ãŒãªã€ã‚«ã‚¿ã‚«ãƒŠã€æ¼¢å­—ï¼ˆCJKçµ±åˆæ¼¢å­—ï¼‰ã®æ­£è¦è¡¨ç¾
  const japaneseRegex = /[\u3040-\u309F\u30A0-\u30FF\u4E00-\u9FAF]/;

  // æ—¥æœ¬èªæ–‡å­—ãŒå«ã¾ã‚Œã¦ã„ã‚‹å ´åˆã¯æ—¥æœ¬èª
  if (japaneseRegex.test(trimmedText)) {
    return 'ja';
  }

  // è‹±èªã®ã‚¢ãƒ«ãƒ•ã‚¡ãƒ™ãƒƒãƒˆãŒå«ã¾ã‚Œã¦ã„ã‚‹å ´åˆã¯è‹±èª
  const englishRegex = /[a-zA-Z]/;
  if (englishRegex.test(trimmedText)) {
    return 'en';
  }

  // ã©ã¡ã‚‰ã§ã‚‚ãªã„å ´åˆï¼ˆæ•°å­—ã®ã¿ãªã©ï¼‰ã¯è‹±èªã‚’ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆã¨ã™ã‚‹
  return 'en';
}

/**
 * è¨€èªã«å¿œã˜ã¦æœ€é©ãªéŸ³å£°ã‚’é¸æŠã™ã‚‹
 * @param lang è¨€èªã‚³ãƒ¼ãƒ‰
 * @returns æœ€é©ãªéŸ³å£°è¨­å®š
 */
export function getOptimalVoiceSettings(lang: 'ja' | 'en'): {
  lang: string;
  voiceURI?: string;
  rate: number;
  pitch: number;
} {
  if (lang === 'ja') {
    return {
      lang: 'ja-JP',
      rate: 0.85, // æ—¥æœ¬èªã¯è‡ªç„¶ãªé€Ÿåº¦ã§
      pitch: 1.0, // æ—¥æœ¬èªã¯è‡ªç„¶ãªãƒ”ãƒƒãƒã§
    };
  } else {
    return {
      lang: 'en-US',
      rate: 0.8, // è‹±èªã¯æ¨™æº–çš„ãªé€Ÿåº¦ã§
      pitch: 1.1, // è‹±èªã¯å°‘ã—é«˜ã‚ã®ãƒ”ãƒƒãƒã§å­ä¾›å‘ã‘ã«
    };
  }
}

/**
 * åˆ©ç”¨å¯èƒ½ãªéŸ³å£°ã‹ã‚‰æœ€é©ãªã‚‚ã®ã‚’é¸æŠã™ã‚‹
 * @param lang è¨€èªã‚³ãƒ¼ãƒ‰
 * @returns æœ€é©ãªéŸ³å£°ã€ã¾ãŸã¯null
 */
export function findBestVoice(lang: 'ja' | 'en'): SpeechSynthesisVoice | null {
  if (!('speechSynthesis' in window)) {
    return null;
  }

  const voices = window.speechSynthesis.getVoices();

  // voices ãŒ undefined ã¾ãŸã¯ null ã®å ´åˆã¯ null ã‚’è¿”ã™
  if (voices === null || voices === undefined || !Array.isArray(voices)) {
    return null;
  }

  const targetLang = lang === 'ja' ? 'ja' : 'en';

  // è¨€èªã«ãƒãƒƒãƒã™ã‚‹éŸ³å£°ã‚’ã™ã¹ã¦å–å¾—
  const matchingVoices = voices.filter(
    (voice) =>
      voice !== null &&
      voice !== undefined &&
      voice.lang !== null &&
      voice.lang !== undefined &&
      voice.lang.toLowerCase().startsWith(targetLang),
  );

  if (matchingVoices.length === 0) {
    // ãƒãƒƒãƒã™ã‚‹éŸ³å£°ãŒãªã„å ´åˆã¯ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆã¾ãŸã¯æœ€åˆã®éŸ³å£°ã‚’è¿”ã™
    const defaultVoice = voices.find(
      (voice) => voice !== null && voice !== undefined && voice.default === true,
    );
    return defaultVoice !== undefined ? defaultVoice : voices.length > 0 ? voices[0] : null;
  }

  // æ—¥æœ¬èªéŸ³å£°ã®å ´åˆã€ç‰¹åˆ¥ãªå„ªå…ˆé †ä½ã§é¸æŠ
  if (lang === 'ja') {
    return selectBestJapaneseVoice(matchingVoices);
  } else {
    return selectBestEnglishVoice(matchingVoices);
  }
}

/**
 * æ—¥æœ¬èªéŸ³å£°ã®ä¸­ã‹ã‚‰æœ€é©ãªã‚‚ã®ã‚’é¸æŠ
 * @param voices æ—¥æœ¬èªéŸ³å£°ã®ãƒªã‚¹ãƒˆ
 * @returns æœ€é©ãªæ—¥æœ¬èªéŸ³å£°
 */
function selectBestJapaneseVoice(voices: SpeechSynthesisVoice[]): SpeechSynthesisVoice {
  // 1. é«˜å“è³ªãªæ—¥æœ¬èªéŸ³å£°ã‚’å„ªå…ˆï¼ˆãƒ‹ãƒ¥ãƒ¼ãƒ©ãƒ«ã‚„ãƒ—ãƒ¬ãƒŸã‚¢ãƒ éŸ³å£°ï¼‰
  const highQualityVoice = voices.find((voice) => {
    const name = voice.name.toLowerCase();
    return (
      name.includes('enhanced') ||
      name.includes('premium') ||
      name.includes('neural') ||
      name.includes('é«˜å“è³ª') ||
      name.includes('premium') ||
      // iOS/macOSã®é«˜å“è³ªæ—¥æœ¬èªéŸ³å£°
      name.includes('kyoko') ||
      name.includes('otoya') ||
      name.includes('siri') ||
      // Windowsã®é«˜å“è³ªæ—¥æœ¬èªéŸ³å£°
      name.includes('haruka') ||
      name.includes('ichiro') ||
      name.includes('sayaka')
    );
  });

  if (highQualityVoice !== undefined) {
    return highQualityVoice;
  }

  // 2. ãƒ­ãƒ¼ã‚«ãƒ«éŸ³å£°ã‚’å„ªå…ˆ
  const localVoice = voices.find((voice) => voice.localService);
  if (localVoice !== undefined) {
    return localVoice;
  }

  // 3. ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆéŸ³å£°
  const defaultVoice = voices.find((voice) => voice.default);
  if (defaultVoice !== undefined) {
    return defaultVoice;
  }

  // 4. æœ€åˆã®éŸ³å£°ã‚’è¿”ã™
  return voices[0];
}

/**
 * è‹±èªéŸ³å£°ã®ä¸­ã‹ã‚‰æœ€é©ãªã‚‚ã®ã‚’é¸æŠ
 * @param voices è‹±èªéŸ³å£°ã®ãƒªã‚¹ãƒˆ
 * @returns æœ€é©ãªè‹±èªéŸ³å£°
 */
function selectBestEnglishVoice(voices: SpeechSynthesisVoice[]): SpeechSynthesisVoice {
  // 1. ãƒ­ãƒ¼ã‚«ãƒ«éŸ³å£°ã‚’å„ªå…ˆ
  const localVoice = voices.find((voice) => voice.localService);
  if (localVoice !== undefined) {
    return localVoice;
  }

  // 2. USè‹±èªã‚’å„ªå…ˆ
  const usVoice = voices.find((voice) => voice.lang.toLowerCase().includes('en-us'));
  if (usVoice !== undefined) {
    return usVoice;
  }

  // 3. ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆéŸ³å£°
  const defaultVoice = voices.find((voice) => voice.default);
  if (defaultVoice !== undefined) {
    return defaultVoice;
  }

  // 4. æœ€åˆã®éŸ³å£°ã‚’è¿”ã™
  return voices[0];
}
